.nav-blurple {
  background: #212529;
}

.navbar {
  padding: 15px 10px;
}

.navbar.dash-nav {
  padding: 12px 0;
}

.navbar.dash-nav .nav-links,
.navbar.dash-nav .navbar-toggle {
  color: #2b2c34;
}

.navbar.dash-nav .navbar-toggle {
  top: 4px;
}

.nav-full {
  background: #0f1113;
}

.main-nav {
  list-style-type: none;
  display: none;
}

.nav-links {
  text-decoration: none;
  font-weight: 700;
  color: #fffffe;
}

.nav-links.active {
  color: #fa5246;
}

.main-nav li {
  text-align: center;
  margin: 15px auto;
}

.logo {
  display: flex;
  align-items: center;
  border: 0;
}

.logo h4 {
  margin: 0;
  margin-left: 14px;
  color: #fffffe;
  font-weight: 700;
}

.logo:hover {
  border: 0;
}

.logo p {
  font-size: 10px;
  background: #d7d8e1;
  padding: 2px 8px;
  border-radius: 5px;
  margin-left: 5px;
}

.navbar-toggle {
  position: absolute;
  top: 14px;
  right: 20px;
  cursor: pointer;
  color: #fffffe;
  font-size: 24px;
}

.active {
  display: block;
}

@media screen and (min-width: 768px) {
  .navbar {
    padding: 30px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .navbar .dash-nav {
    padding: 20px 0;
  }

  .navbar .navbar-toggle {
    display: none;
  }

  .navbar .logo {
    margin-top: 0;
  }

  .navbar .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    align-items: center;
    margin: 0;
  }

  .navbar .main-nav li {
    margin: 0;
    margin-left: 20px;
  }

  .navbar .main-nav li.nav-divider {
    width: 2px;
    background: #fffffe;
    height: 20px;
  }

  .navbar .main-nav li.nav-divider.divider-dark {
    background: #2b2c34;
  }

  .navbar .main-nav li a.active {
    color: #fa5246;
    border: 2px solid transparent;
    text-decoration: none;
  }
}

.footer {
  background: #2b2c34;
  padding: 0 0 100px;
}

.footer .footer-contents {
  padding-top: 80px;
}

.footer .footer-contents .footer-social a {
  display: inline-block;
  font-size: 1.5em;
}

.footer h5 {
  color: #fffffe;
}

.footer h5 a {
  margin: 0;
}

.footer .builtby p,
.footer .builtby svg {
  display: inline-block;
}

.footer p {
  color: #d7d8e1;
  font-size: 0.8em;
  margin: 15px 0;
}

.footer a {
  color: #fffffe;
  display: block;
}

.footer a:hover {
  color: #3498db;
  cursor: pointer;
}

.avatar {
  font-size: 0.8rem;
  height: 1.6rem;
  width: 1.6rem;
  background: #212529;
  border-radius: 50%;
  color: rgba(245, 246, 255, 0.85);
  display: inline-block;
  font-weight: 300;
  line-height: 1.25;
  margin: 0;
  position: relative;
  vertical-align: middle;
}

.avatar.avatar-xs {
  font-size: 0.4rem;
  height: 0.8rem;
  width: 0.8rem;
}

.avatar.avatar-sm {
  font-size: 0.6rem;
  height: 1.2rem;
  width: 1.2rem;
}

.avatar.avatar-lg {
  font-size: 1.2rem;
  height: 2.4rem;
  width: 2.4rem;
}

.avatar.avatar-xl {
  font-size: 1.6rem;
  height: 3.2rem;
  width: 3.2rem;
}

.avatar.avatar-commsor-lg {
  font-size: 8rem;
  height: 16rem;
  width: 16rem;
}

.avatar img {
  border-radius: 50%;
  height: 100%;
  position: relative;
  width: 100%;
  z-index: 1;
}

.avatar .avatar-icon,
.avatar .avatar-presence {
  background: #f5f6ff;
  bottom: 14.64%;
  height: 50%;
  padding: 0.1rem;
  position: absolute;
  right: 14.64%;
  transform: translate(50%, 50%);
  width: 50%;
  z-index: 2;
}

.avatar .avatar-presence {
  background: #d7d8e1;
  box-shadow: 0 0 0 0.1rem #f5f6ff;
  border-radius: 50%;
  height: 0.5em;
  width: 0.5em;
}

.avatar .avatar-presence.online {
  background: #6cbc2b;
}

.avatar .avatar-presence.busy {
  background: #fa5246;
}

.avatar .avatar-presence.away {
  background: #fc0;
}

.avatar[data-initial]::before {
  color: currentColor;
  content: attr(data-initial);
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.breadcrumb {
  list-style: none;
  margin: 0.2rem 0;
  padding: 0.2rem 0;
}

.breadcrumb .breadcrumb-item {
  color: #d7d8e1;
  display: inline-block;
  margin: 0;
  padding: 0.2rem 0;
}

.breadcrumb .breadcrumb-item:not(:last-child) {
  margin-right: 0.2rem;
}

.breadcrumb .breadcrumb-item:not(:last-child) a {
  color: #d7d8e1;
}

.breadcrumb .breadcrumb-item:not(:last-child) a:hover {
  color: #fffffe;
}

.breadcrumb .breadcrumb-item:last-child {
  margin-right: 0.2rem;
}

.breadcrumb .breadcrumb-item:last-child a {
  color: #fffffe;
}

.breadcrumb .breadcrumb-item:not(:first-child)::before {
  color: #d7d8e1;
  content: "/";
  padding-right: 0.4rem;
}

.card-link {
  color: #2b2c34;
  font-weight: 400;
  position: relative;
  transition: all ease 0.3s;
}

.card-link:hover {
  color: #3498db;
  -moz-transform: translateY(-4px);
  -ms-transform: translateY(-4px);
  -o-transform: translateY(-4px);
  -webkit-transform: translateY(-4px);
  transform: translateY(-4px);
}

.card {
  border: 1px solid #f5f6ff;
  background: #fffffe;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 6px rgba(55, 65, 80, 0.04);
  margin: 10px 0;
}

.card.card-hover {
  display: block;
  top: 0px;
  position: relative;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
  border: 1px solid #f5f6ff;
  transition: all 0.2s ease;
}

.card.card-hover:hover {
  cursor: pointer;
  box-shadow: 0px 4px 8px rgba(45, 66, 70, 0.1);
  top: -4px;
  border: 1px solid #fa5246;
  background-color: white;
}

.card.card-hover.active-notifications {
  padding-bottom: 18px;
}

.card.card-hover.active-notifications:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -16px;
  right: -16px;
  background: #fa5246;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  transform: scale(2);
  transform-origin: 50% 50%;
  transition: transform 0.15s ease-out;
}

.card.card-hover:hover:before {
  transform: scale(2.15);
}

.card .card-header,
.card .card-body,
.card .card-footer {
  padding: 0.8rem;
  padding-bottom: 0;
}

.card .card-header:last-child,
.card .card-body:last-child,
.card .card-footer:last-child {
  padding-bottom: 0.8rem;
}

.card .card-body {
  flex: 1 1 auto;
}

.card .card-body.card-body-h-center {
  display: flex;
  align-items: center;
}

.card .card-image {
  padding-top: 0.8rem;
  position: relative;
}

.card .card-image:first-child {
  padding-top: 0;
}

.card .card-image:first-child img {
  border-top-left-radius: 0.2rem;
  border-top-right-radius: 0.2rem;
}

.card .card-image:last-child img {
  border-bottom-left-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
}

.card .card-image .img-overlay.top-right {
  position: absolute;
  top: 3px;
  right: 6px;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown .dropdown-toggle {
  color: #2b2c34;
  border-bottom: 2px solid transparent;
  font-weight: 700;
}

.dropdown .dropdown-toggle.dropdown-link {
  color: #fffffe;
  border-bottom: none;
}

.dropdown .dropdown-toggle.dropdown-link:hover {
  border-bottom: none;
  color: #fa5246;
}

.dropdown .menu {
  animation: slide-down 0.15s ease 1;
  display: none;
  margin-top: -5px;
  left: 0;
  max-height: 50vh;
  overflow-y: auto;
  position: absolute;
  top: 100%;
}

.dropdown .menu li {
  margin: 0;
}

.dropdown .menu a {
  border: none;
}

.dropdown .menu a:hover {
  border: none;
}

.dropdown.dropdown-right .menu {
  left: auto;
  right: 0;
}

.dropdown.active .menu,
.dropdown .dropdown-toggle:focus + .menu,
.dropdown .menu:hover {
  display: block;
}

.dropdown.active .menu,
.dropdown .dropdown-toggle:hover + .menu,
.dropdown .menu:hover {
  display: block;
}

.dropdown .btn-group .dropdown-toggle:nth-last-child(2) {
  border-bottom-right-radius: 0.2rem;
  border-top-right-radius: 0.2rem;
}

.menu {
  box-shadow: 0 0.05rem 0.2rem rgba(43, 44, 52, 0.3);
  background: #fffffe;
  border-radius: 0.2rem;
  list-style: none;
  margin: 0;
  min-width: 180px;
  padding: 0.4rem;
  transform: translateY(0.2rem);
  z-index: 300;
}

.menu.menu-lg {
  width: 400px;
  padding: 0.4rem;
}

.menu.menu-lg .product-link {
  margin: 0 0 5px;
  padding: 0.4rem;
}

.menu.menu-lg .product-link:hover {
  background: #fff;
}

.menu.menu-lg .product-link:last-child {
  margin: 0;
}

.menu.menu-nav {
  background: transparent;
  box-shadow: none;
}

.menu .menu-item {
  margin-top: 0;
  padding: 0 0.4rem;
  position: relative;
  text-decoration: none;
  text-align: left;
}

.menu .menu-item > a {
  border-radius: 0.2rem;
  color: #2b2c34;
  display: block;
  margin: 0 -0.4rem;
  padding: 0.2rem 0.4rem;
  text-decoration: none;
}

.menu .menu-item > a:focus,
.menu .menu-item > a:hover {
  background: #d1d1e9;
  color: #212529;
}

.menu .menu-item > a:active,
.menu .menu-item > a.active {
  background: #d1d1e9;
  color: #212529;
}

.menu .menu-item .form-checkbox,
.menu .menu-item .form-radio,
.menu .menu-item .form-switch {
  margin: 0.1rem 0;
}

.menu .menu-item + .menu-item {
  margin-top: 0.2rem;
}

.menu .menu-badge {
  align-items: center;
  display: flex;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
}

.menu .menu-badge .label {
  margin-right: 0.4rem;
}

.nav {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0.2rem 0;
  padding: 0;
}

.nav .nav-item {
  padding: 0.8rem 1.2rem;
  font-weight: 600;
  border-radius: 0.2rem;
  transition: 0.4s;
  margin: 0;
}

.nav .nav-item a {
  color: #9b9caf;
  text-decoration: none;
  border-bottom: none;
}

.nav .nav-item a:hover {
  color: #212529;
  text-decoration: none;
  border-bottom: none;
}

.nav .nav-item.active {
  background: #fffffe;
  box-shadow: 0 4px 6px rgba(55, 65, 80, 0.04);
}

.nav .nav-item.active > a {
  color: #fa5246;
  font-weight: bold;
}

.nav .nav-item.active > a:focus,
.nav .nav-item.active > a:hover {
  color: #212529;
}

