<template>
  <div class="introduce_container">
    <div class="quetion_container">
      <div class="introduce_text">저에 대해 자주 묻는 질문</div>
      <div id="staggered-list-demo">
        <div class="query_box">
          <input
            v-model="query"
            class="FAQ_input"
            placeholder="해시태그를 입력해주세요."
          />
        </div>
      </div>
      <transition-group
        name="staggered-fade"
        tag="ul"
        v-bind:css="false"
        v-on:before-enter="beforeEnter"
        v-on:enter="enter"
        v-on:leave="leave"
      >
        <div class="helpdesk_container">
          <ul class="helpdesk_ul" style="list-style: none">
            <li
              class="helpdesk_li"
              v-for="(item, index) in computedList"
              v-bind:key="item.msg"
              v-bind:data-index="index"
            >
              <div class="helpdesk_li_div">
                <button
                  @click="item[`show${index}`] = !item[`show${index}`]"
                  class="helpdesk_button"
                >
                  <div class="helpdesk_li_div_button_div">
                    <span> </span>
                    <strong class="strong_text">{{ item.Q }}</strong>
                  </div>
                  <div class="hashtag">#{{ item.msg }}</div>
                </button>
              </div>
              <div id="demo">
                <transition name="fade">
                  <div v-if="item[`show${index}`]" class="answer_box">
                    <p>{{ item.answer }}</p>
                  </div>
                </transition>
              </div>
            </li>
          </ul>
        </div>
      </transition-group>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "app",
  data() {
    return {
      query: "",
      list: [
        {
          msg: "김민경",
          Q: "김민경은 누구인가요?",
          answer:
            "그로스 마케터가 되기 위하여 성장 중이며, 마케팅, 개발, 기획, 데이터 등 다양한 방면을 다룰 수 있는 다재다능한 인물입니다.",
          show: false,
        },
        {
          msg: "직무",
          Q: "어떤 직무가 가능한가요?",
          answer:
            "주요 업무는 마케팅입니다. 하지만 시간과 인력이 부족하다면 개발, 기획팀에 대체인력으로 투입이 가능한 하이브리드형 인재입니다.",
          show: false,
        },
        {
          msg: "직무능력",
          Q: "직무 능력은 어느 정도인가요?",
          answer:
            "직무 능력의 자세한 사항은 아래 표에서 확인해주세요. 포트폴리오와 자기소개서를 보시면 더욱 정확히 파악하실 수 있습니다.",
          show: false,
        },
        {
          msg: "자기소개서",
          Q: "자기소개서는 어디 있나요?",
          answer:
            "자기소개서는 상단 카테고리를 확인하시면 이동하실 수 있습니다.",
          show: false,
        },
        {
          msg: "포트폴리오",
          Q: "포트폴리오는 어디서 확인하나요?",
          answer:
            "포트폴리오의 경우에는 상단 카테고리에서 '마케터'를 클릭하시면 확인가능합니다. 개발자 포트폴리오는 좌측 카테고리에서 프로젝트를 선택하시면 됩니다.",
          show: false,
        },
        {
          msg: "업무경력",
          Q: "어떤 경력이 있나요?",
          answer:
            "네이버 스마트스토어를 2년 간 운영하며, 제품 기획, 판매, 마케팅 등 전반적인 업무를 수행하였습니다.",
          show: false,
        },
      ],
    };
  },

  computed: {
    computedList: function () {
      var vm = this;
      return this.list.filter(function (item) {
        return item.msg.toLowerCase().indexOf(vm.query.toLowerCase()) !== -1;
      });
    },
  },

  methods: {
    beforeEnter: function (el) {
      el.style.opacity = 0;
      el.style.height = 0;
    },
    enter: function (el, done) {
      var delay = el.dataset.index * 150;
      setTimeout(function () {
        Velocity(el, { opacity: 1, height: "1.6em" }, { complete: done });
      }, delay);
    },
    leave: function (el, done) {
      var delay = el.dataset.index * 150;
      setTimeout(function () {
        Velocity(el, { opacity: 0, height: 0 }, { complete: done });
      }, delay);
    },

    introduce_event: function () {
      let scrollpos = window.scrollY;
      const introduce_text = document.querySelector(".introduce_container");
      const add_class_on_scroll = (dom) => dom.classList.add("come-in");
      window.addEventListener("scroll", function () {
        scrollpos = window.scrollY;
        if (scrollpos >= introduce_text.offsetHeight - 200)
          add_class_on_scroll(introduce_text);
      });
    },
  },
  components: {},
};
</script>

<style scoped>
@font-face {
  font-family: "jua";
  src: url("../../public/font/BMJUA_ttf.ttf");
}

@font-face {
  font-family: "dohyeon";
  src: url("../../public/font/BMDOHYEON_ttf.ttf");
}
.introduce_container {
  width: 100%;
  background-color: #edeef0;
}

.quetion_container {
  width: 100%;
  background-color: #d1dfe8;
  padding: 5rem 0rem;
}
.query_box {
  font-family: "jua";
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.FAQ_input {
  margin: 10px;
  margin: 0px 10px;
  font-size: 1.5rem;
  width: 30%;
  border: none;
  border-bottom: 2px solid black;
  background-color: transparent;
}

.introduce_text {
  margin: 4rem 0rem 3rem 0rem;
  display: inline-block;
  font-family: "dohyeon";
  font-size: 3rem;
  font-weight: 600;
  color: #e9a8ab;
}

.helpdesk_container {
  margin-right: 70px;
  display: table;
  position: relative;
  left: -3rem;
  width: 100%;
  height: 100%;
  min-height: 100%;
  table-layout: fixed;
  text-align: left;
  box-sizing: border-box;
}

.helpdesk_li {
  display: inline-block;
  width: 25%;
  margin-left: 120px;
  margin-bottom: 10px;
  vertical-align: top;
}

.helpdesk_li_div {
  margin: 0 15px;
  height: 142px;
}

.helpdesk_button {
  display: block;
  width: 100%;
  padding: 22px 21px;
  text-align: left;
  background-color: #c2d5e3;
  border: 2px solid #edeef0;

  border-radius: 10px;
  box-shadow: 3px 3px 3px #edeef0;
  cursor: pointer;
}

.strong_text {
  color: black;
  font-size: medium;
  letter-spacing: 0.5px;
  font-family: "dohyeon";
}
.helpdesk_button:hover {
  transform: scale(1.1);
  background-color: #edeef0;
  transition: 0.2s linear;
}

.helpdesk_li_div_button_div {
  display: flex;
  align-items: center;
  overflow: hidden;
  word-break: break-all;
  white-space: normal;
  text-overflow: ellipsis;
  line-height: 24px;
  max-height: 48px;
  height: 5rem;
  max-height: 5em;
  line-height: 1rem;
}

span {
  display: inline-block;
  width: 15px;
  height: 23px;
  margin: 0px 5px 5px 0px;
  background: url(https://ssl.pstatic.net/static/help/new_help/img/svg/faq_q.svg)
    no-repeat;
  background-size: 15px 23px;
  /* vertical-align: top; */
}
.hashtag {
  cursor: pointer;
  padding: 3px 5px;
  display: inline-block;
  background-color: #ffc400;
  color: #00227e;
  border: none;
  border-radius: 3px;
  font-family: "jua";
  font-weight: 200;
  font-size: medium;
  box-shadow: 3px 3px 2px #f39945;
}

.hashtag:hover {
  background-color: #00227e;
  color: #ffc400;
  transform: scale(1.1);
  transition: 0.2s linear;
  box-shadow: 3px 3px 2px #001348;
}

.another_help_container {
  width: 100%;
  justify-content: space-evenly;
  margin: 40px 0px;
}

.intro_box1,
.intro_box2 {
  display: flex;
  justify-content: center;
}
.another_help_box {
  margin: 2rem;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  white-space: normal;
  vertical-align: top;
  border: 4px solid #a3b6c5;
  background-color: #edeef0;
  box-shadow: 3px 3px 3px #5f7d94;
  border-radius: 10px;
  cursor: pointer;
  width: 20%;
  padding: 22px 21px;
}

.another_help_box:hover {
  background-color: #a3b6c5;

  color: white;
  transform: scale(1.05);
  transition: 0.2s linear;
}

.intro_sub_text {
  display: inline-block;
  font-family: "jua";
  font-size: 2rem;
  font-weight: 300;
  margin-bottom: 12px;
  border-bottom: 2px solid #5f7d94;
}

.help_time {
  font-family: "jua";
  font-size: medium;
  font-weight: 200;
  margin-bottom: 5px;
}

.help_time_info {
  font-family: "jua";
  font-size: medium;
  font-weight: 100;
  color: #008e37;
}

.answer_box {
  width: 100%;
  font-family: "jua";
  font-size: 1rem;
  padding: 1rem;
  position: relative;
  word-break: keep-all;
  line-height: 2rem;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.modal {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  display: none;

  /* background-color: rgba(0, 0, 0, 0.4); */
}
.modal.show {
  display: block;
}

.chating_screen {
  width: 100%;
  background-color: white;
  border: 2px solid black;
  border-radius: 15px;
  box-shadow: 3px 3px 3px black;
}
</style>
